<template>
  <div>
    <el-form :inline="true" :model="params" class="demo-form-inline" ref="filterFormRef">
      <el-form-item label="财务组织" size="mini">
        <el-select clearable v-model="params.company_id" placeholder="请选择" filterable @focus="companyListInvok" maxlength="100" @change="companyChange">
          <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
            <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="供应商(厂家)" size="mini">
        <el-select clearable v-model="params.supplier_id" maxlength="100" @change="supplierChangesave" :filter-method="supplierListInvok" @focus="supplierListInvok" filterable placeholder="请选择">
          <el-option v-for="item in supplierList" :label="item.supplier_name" :value="item.supplier_id" :key="item.supplier_id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item size="mini" class="search-style search">
        <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
        <el-button plain @click="onReset" size="mini">重置</el-button>
      </el-form-item>
    </el-form>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" ref="table1" border :data="TableData.tableData" :summary-method="getSummaries" show-summary :max-height="tableHeight" style="width: 100%">
        <el-table-column :show-overflow-tooltip="true" property="registe_time" label="操作" fixed="right">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small" v-if="authority.b_info==1">查看</el-button>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="company_name" label="财务组织" width="200"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="supplier_name" label="供应商(厂家)" width="200"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="deposits" label="预付款帐户余额" width="200" align="right">
          <template slot-scope="scope">
            <pre>{{scope.row.deposits | priceFormatNone}}</pre>
          </template>
        </el-table-column>
      </el-table>

      <div class="table_sum" ref='table_sum'>
        <ul style="overflow:auto;width:4000px;">
          <li style="width:200px;text-align:left; padding-left:10px;">
            总计
          </li>
          <li style="width:200px"></li>
          <li style="width:200px;text-align:right">{{allCountBalance}}</li>
          <li style="width:200px"></li>
        </ul>
      </div>
    </div>
    <div class="page_container">
      <div class="pageNumber">每页显示</div>
      <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import {
  getData,
  tableResize //明细表去除页面滚动条
} from "common/utils/public_function/commonUtils";
export default {
  name: "purchaseFactoryprepaiddefault",
  data() {
    return {
      authority: {},
      paramsSearch: {},
      TableData: {
        tableData: [{}]
      },
      allCountBalance: 0, //总计
      tableHeight: 600
    };
  },
  methods: {
    supplierChangesave(val) {
      if (val) {
        this.params.supplier_name = this.supplierList.filter(
          obj => obj.supplier_id === val
        )[0].supplier_name;
      }
    },
    handleSizeChange(val) {
      Object.assign(this.paramsSearch.params, {
        per_page: val,
        page: 1
      });
      getData(this.paramsSearch, 3);
    },
    handleCurrentChange(val) {
      Object.assign(this.paramsSearch.params, {
        page: val
      });
      getData(this.paramsSearch, 3);
    },
    onSearch() {
      Object.assign(this.paramsSearch.params, this.params, {
        page: 1
      });
      getData(this.paramsSearch, 3);
    },
    companyChange(val) {
      if (val != "") {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
      this.$set(this.params, "supplier_id", "");
    },
    onReset() {
      this.$set(this, "params", {});
      this.$set(this, "paramsSearch", {
        params: this.params, //1默认带的参数
        TableData: this.TableData, //列表数据
        API: "getPrepaidDetailInfo", //接口名
        vm: this //传递的this
      });
      this.onSearch();
    },
    handleClick(row) {
      this.$router.push({
        path: "/purchase/factoryprepaid/factoryprepaidSearch",
        query: {
          company_name: row.company_name,
          supplier_name: row.supplier_name,
          supplier_id: row.supplier_id,
          company_id: row.company_id
        }
      });
    },
    getSummaries(param) {
      // 合并计算
      const { columns, data } = param;
      const sums = [];
      const depositsArr = data.map(item => Number(item["deposits"]));
      let depositsArrSum = "";
      if (!depositsArr.every(value => isNaN(value))) {
        depositsArrSum = depositsArr.reduce((prev, curr) => {
          const value = Number(curr);
          if (!isNaN(value)) {
            return prev + curr;
          } else {
            return prev;
          }
        }, 0);
      }
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "本页小计";
          return;
        }
        if (column.property === "deposits") {
          // sums[index] = this.thisPageBalance;(以前使用字段)
          sums[index] = Number(depositsArrSum).toFixed(2);
        } else {
          sums[index] = "";
        }
      });
      return sums;
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    const elTable1 = this.$refs.table1.$el.getElementsByClassName(
      "el-table__body-wrapper"
    )[0];
    const elTable2 = this.$refs.table_sum;
    elTable1.addEventListener("scroll", function() {
      elTable2.scrollLeft = this.scrollLeft;
    });
    this.$set(this, "paramsSearch", {
      params: this.params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getPrepaidDetailInfo", //接口名
      vm: this //传递的this
    });
    getData(this.paramsSearch, 3, tableResize.bind(this));
    //取消单据右侧滚动条
    window.onresize = tableResize.bind(this);
  }
};
</script>

